



































































































































  import addContact from '../../components/addContact.vue'
  import contact from '../../components/contact.vue'
  import rewardRule from '../../components/rewardRule.vue'
  import poster from './poster.vue'
  
	export default {
    components: {
      addContact,
      contact,
      rewardRule,
      poster
    },
		data() {
			return {
        bankId: '',
        bankCode: '',
        bankInfo: null,
        reqing: false,
				advLists: [],
        popAct: false,
        actIndex: 0,
        cardLists: [],
        addData: {},
        contactData: {},
        rewardData: {},
        popShareType: false,
        posterData: {},
        shorturl: '',
        clickShareLinking: false
			};
		},
    computed: {
    	userInfo() {
    		return this.$store.state.userInfo
    	}
    },
		onLoad(query) {
      this.bankId = query.bankId
      this.bankCode = query.bankCode
      // 银行详情
      this.$http({
        url: this.$apis.bankInfo,
        method: 'GET',
        data: {
          id: this.bankId
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.bankInfo = data.data
            uni.setNavigationBarTitle({
            	title: this.bankInfo.name
            })
            if (uni.getStorageSync('createCredit') === true) {
              this.gotoHandle()
              uni.removeStorageSync('createCredit')
            }
          } else {
            uni.setNavigationBarTitle({
            	title: '银行'
            })
          }
        }
      })
      // 文章
      this.$http({
        url: this.$apis.articles,
        method: 'GET',
        data: {
          cat_id: 5,
          bank_id: this.bankId
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.advLists = data.data
            this.advLists.forEach((item, index) => {
              this.$set(item, 'index', index)
            })
          }
        }
      })
      // 信用卡
      this.$http({
      	url: this.$apis.card,
      	method: 'GET',
      	data: {
      		bankid: this.bankId
      	},
      	cb: (err, data) => {
      		if (!err && data.success) {
      			this.cardLists = data.data
      			this.cardLists.forEach(item => {
      				this.$set(item, 'tagArr', this.handleStr(item.show_tags))
      			})
      		}
      	}
      })
		},
    onShow() {
      if (uni.getStorageSync('createCredit') === true && this.bankInfo) {
        this.gotoHandle()
        uni.removeStorageSync('createCredit')
      }
    },
    methods: {
      handleStr(str) {
      	return str.split(',')
      },
      showRule(item) {
        this.rewardData = {
          show: true,
          total: item.earn,
          conditionTitle: item.clearing_title,
          conditionDec: item.clearing_info
        }
      },
      gotoIndex() {
        uni.switchTab({
        	url: '/pages/shop/index'
        })
      },
      gotoGuide() {
        this.$navigateTo({
        	url: './guide?bankId=' + this.bankInfo.id + '&bankCode=' + this.bankInfo.code
        })
      },
      showAct(index) {
        this.popAct = true
        this.actIndex = index
      },
      hideAct() {
        this.popAct = false
      },
      gotoAdd() {
      	this.addData = {
      		show: true,
      		productId: this.bankInfo.id,
      		state: 0,
      		type: 'xyk'
      	}
      },
      gotoHandle() {
        if (this.reqing) return
        this.reqing = true
        this.$http({
        	url: this.$apis.contactList,
        	method: 'GET',
        	data: null,
        	cb: (err, data) => {
        		if (err || data.data.data.length <= 0) {
        			this.gotoAdd()
        		} else {
        			this.contactData = {
        				show: true,
        				productId: this.bankInfo.id,
        				state: 0,
        				type: 'xyk',
        				index: -1
        			}
        		}
        		this.reqing = false
        	}
        })
      },
      gotoCard(id) {
      	this.$navigateTo({
      		url: '/pages/credit/card?id=' + id
      	})
      },
      showSharePop() {
        this.popShareType = true
      },
      hideSharePop() {
        this.popShareType = false
      },
      clickShareLink() {
        if (this.clickShareLinking) {
          uni.showToast({
          	title: '分享中，请勿频繁操作',
            icon: 'none'
          })
          return
        }
        this.clickShareLinking = true
        let str = this.$constant.shareUrl + '?pid=' + this.userInfo.id
        uni.getProvider({
        	service: 'share',
          success: res => {
            if (~res.provider.indexOf('weixin')) {
              uni.share({
              	provider: 'weixin',
              	type: 0,
                title: this.$store.state.config.system_nick_name,
                scene: 'WXSceneSession',
                summary: this.$store.state.config.platform_info,
                href: str,
                imageUrl: this.$store.state.config.logo,
              	success: res => {
                  console.log(res)
                },
              	fail: err => {
                  this.copy(str)
                },
              	complete: () => {
                  this.clickShareLinking = false
                }
              });
            } else {
              this.clickShareLinking = false
              this.copy(str)
            }
          },
          fail: () => {
            this.clickShareLinking = false
          	this.copy(str)
          }
        })
      },
      clickSharePoster() {
        this.hideSharePop()
        this.posterData = {
          show: true,
          code: this.bankInfo.code,
          posterUrl: this.bankInfo.background_img,
          url: this.$constant.shareUrl + '?pid=' + this.userInfo.id
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '链接已复制，请手动粘贴发送给好友',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideSharePop()
          }
        })
      }
    }
	}
